import React, { Component } from 'react';
import exm from './exm.png';
import load from './load.gif';
import './App.css';
import { getDefaultNormalizer } from '@testing-library/react';
import hero from './v1.json';

class List extends Component{
    constructor(){
        super();
    }

    handleChange(event){
        var url = "";
        for(var i = 0; i < hero.result.heroes.length; i++){
            if(parseInt(event.target.value) == hero.result.heroes[i].id){
                url = hero.result.heroes[i].img;
                break;
            }
        }

        if(url == undefined){
            url = "https://gamepedia.cursecdn.com/dota2_gamepedia/5/55/Pudge_minimap_icon.png?version=890c2a63b887f2d885caf173289981cc";
        }

        this.props.hero_ref.current.style.backgroundImage = `url("${url}")`;
        console.log(this.props.hero_ref);
        this.props.action(this.props.hero_id, this.props.hero_side, event.target.value);
        console.log(this.props.hero_id, this.props.hero_side, event.target.value);
    }

    render(){
        return(
            <select onChange={this.handleChange.bind(this)}>
                <option value="-1">Hero</option>
                {hero.result.heroes.map((item, index) => <option onChange={this.handleChange} key={index} value={item.id}>{item.localized_name}</option>)}
            </select>
        );
    }
}

